import React, { useState, useEffect } from "react";
import { Table, Button, Form } from "react-bootstrap";
import { FaChevronDown, FaChevronUp, FaEye, FaUsers } from "react-icons/fa";
import { Pagination } from "antd";

const SubUserTable = ({
  users,
  totalCount,
  onUserClick,
  onTransferClick,
  onReverseTransferClick,
  onReportClick,
  onPageChange,
  handleReportClickmainreportasmzsm,
  level = 0,
  initialPage = 1,
  initialPageSize = 10,
}) => {
  const [currentPage, setCurrentPage] = useState(initialPage);
  const [pageSize, setPageSize] = useState(initialPageSize);
  const [subUserPages, setSubUserPages] = useState({});

  useEffect(() => {
    setCurrentPage(initialPage);
    setPageSize(initialPageSize);
  }, [initialPage, initialPageSize]);

  const handlePageChange = async (page, size) => {
    setCurrentPage(page);
    if (size !== pageSize) setPageSize(size);
    await onPageChange(page, size);
  };

  const handleSubUserPageChange = async (userId, page, size) => {
    setSubUserPages((prev) => ({
      ...prev,
      [userId]: { page, size },
    }));
  };

  if (!users?.length) {
    return (
      <div className="text-center py-4">
        <FaUsers size={24} className="mb-2" style={{ color: "var(--bs-primary2)" }} />
        <p style={{ color: "var(--bs-primary2)" }}>No downstream members available</p>
      </div>
    );
  }

  return (
    <div
      className="border rounded my-2"
      style={{
        backgroundColor: "var(--bs-primary1)",
        border: "1px solid var(--bs-primary2)",
        borderRadius: "16px",
        width: "100%",
      }}
    >
      <div
        className="p-2 border-bottom d-flex justify-content-between align-items-center"
        style={{
          backgroundColor: "var(--bs-primary3)",
          color: "var(--bs-primary2)",
          borderBottom: "1px solid var(--bs-primary2)",
          borderRadius: "16px 16px 0 0",
          width: "100%",
        }}
      >
        <h6
          className="mb-0 text-center w-100"
          style={{
            color: "var(--bs-primary2)",
            fontWeight: 600,
          }}
        >
          Downstream Members (Level {level + 1})
        </h6>

        {/* 
        <Form.Select 
          style={{ width: '150px' }}
          value={pageSize}
          onChange={e => handlePageChange(1, Number(e.target.value))}
        >
          <option value={10}>10 per page</option>
          <option value={20}>20 per page</option>
          <option value={50}>50 per page</option>
          <option value={100}>100 per page</option>
        </Form.Select>
        */}
      </div>

      <Table
        hover
        className="mb-0"
        style={{
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          border: "1px solid var(--bs-primary2)",
          borderRadius: "12px",
          overflow: "hidden",
        }}
      >
        <thead
          style={{
            backgroundColor: "var(--bs-primary3)",
            color: "var(--bs-primary2)",
            fontWeight: 600,
            borderBottom: "1px solid var(--bs-primary2)",
            textAlign: "center",
          }}
        >
          <tr
            style={{
              backgroundColor: "var(--bs-primary3)"
            }}
          >
            <th className="text-center">S.No</th>
            <th className="text-center">Refer ID</th>
            <th className="text-center">Name</th>
            <th
              className="text-center"
              style={{ whiteSpace: "nowrap" }}
            >
              User Type
            </th>

            <th className="text-center">Mobile no</th>
            <th className="text-center">Email Id</th>
            <th className="text-center">Status</th>
            <th className="text-center">Downstream</th>
            <th className="text-center">Actions</th>
            <th className="text-center">Wallet</th>
            <th className="text-center" colSpan="2">Report</th>

          </tr>
        </thead>


        <tbody>
          {users.map((user, index) => (
            <SubUserRow
              key={user._id}
              user={user}
              index={index + (currentPage - 1) * pageSize}
              onUserClick={onUserClick}
              onTransferClick={onTransferClick}
              onReverseTransferClick={onReverseTransferClick}
              onReportClick={onReportClick}
              level={level}
              currentPage={subUserPages[user._id]?.page || 1}
              pageSize={subUserPages[user._id]?.size || 10}
              onPageChange={(page, size) =>
                handleSubUserPageChange(user._id, page, size)
              }
              handleReportClickmainreportasmzsm={handleReportClickmainreportasmzsm}
            />
          ))}
        </tbody>
      </Table>

      {totalCount > pageSize && (
        <div
          className="p-3 border-top"
          style={{
            borderTop: "1px solid var(--bs-primary2)",
            backgroundColor: "var(--bs-primary1)",
          }}
        >
          <Pagination
            current={currentPage}
            total={totalCount}
            pageSize={pageSize}
            onChange={(page) => handlePageChange(page, pageSize)}
            showSizeChanger={true}
            onShowSizeChange={(current, size) => handlePageChange(1, size)}
            showQuickJumper={true}
            showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} items`}
            className="justify-content-end"
          />
        </div>
      )}
    </div>
  );
};

const SubUserRow = ({
  user,
  index,
  onUserClick,
  onTransferClick,
  onReverseTransferClick,
  onReportClick,
  level,
  currentPage,
  pageSize,
  onPageChange,
  handleReportClickmainreportasmzsm,
}) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [subUsers, setSubUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [subUserTotalCount, setSubUserTotalCount] = useState(0);

  const fetchSubUsers = async (page, size) => {
    setLoading(true);
    try {
      const result = await onUserClick(user._id, page, size);
      if (result?.data) {
        setSubUsers(result.data);
        setSubUserTotalCount(result.totalCount);
      }
    } catch (error) { }
    setLoading(false);
  };

  const handleExpand = async () => {
    if (!isExpanded && !subUsers.length) await fetchSubUsers(currentPage, pageSize);
    setIsExpanded(!isExpanded);
  };

  const handleSubUserPageChange = async (page, size) => {
    await fetchSubUsers(page, size);
    onPageChange(page, size);
  };

  return (
    <React.Fragment>
      <tr>
        <td>{index + 1}</td>
        <td>{user?.refer_id}</td>
        <td>{user?.name}</td>

        <td>
          <span
            className="badge"
            style={{
              backgroundColor: "var(--bs-primary3)",
              color: "var(--bs-primary2)",
              padding: "6px 10px",
              borderRadius: "10px",
              fontWeight: 600,
            }}
          >
            {user?.user_type}
          </span>
        </td>

        <td>{user?.mobile}</td>
        <td>{user?.email}</td>

        <td>
          <span
            className="badge"
            style={{
              backgroundColor: user.is_approved ? "var(--bs-success)" : "var(--bs-warning)",
              color: "#000",
              padding: "6px 10px",
              borderRadius: "10px",
              fontWeight: 600,
            }}
          >
            {user?.is_approved ? "Approved" : "Pending"}
          </span>
        </td>

        <td>
          {user.user_type !== "Retailer" && (
            <Button
              onClick={handleExpand}
              disabled={loading}
              className="d-flex align-items-center"
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                border: "1px solid var(--bs-primary2)",
                borderRadius: "10px",
                padding: "4px 12px",
                fontWeight: 500,
              }}
            >
              {loading ? "Loading..." : "View"}
              {isExpanded ? (
                <FaChevronUp className="ms-1" style={{ color: "var(--bs-primary2)" }} />
              ) : (
                <FaChevronDown className="ms-1" style={{ color: "var(--bs-primary2)" }} />
              )}
            </Button>
          )}
        </td>

        <td>
          <div className="d-flex gap-2">
            <Button
              size="sm"
              onClick={() => onTransferClick(user)}
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                border: "1px solid var(--bs-primary2)",
                borderRadius: "10px",
                fontWeight: 500,
                padding: "4px 12px",
              }}
            >
              Transfer
            </Button>

            <Button
              size="sm"
              onClick={() => onReverseTransferClick(user)}
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                border: "1px solid var(--bs-primary2)",
                borderRadius: "10px",
                fontWeight: 500,
                padding: "4px 12px",
              }}
            >
              Reverse
            </Button>
          </div>
        </td>

        <td>â‚¹{user?.main_wallet || 0}</td>

        <td>
          <Button
            onClick={() => onReportClick(user._id)}
            style={{
              backgroundColor: "transparent",
              border: "1px solid var(--bs-primary2)",
              borderRadius: "10px",
              padding: "6px 10px",
            }}
          >
            <FaEye style={{ color: "black" }} />
          </Button>
        </td>

        <td style={{ whiteSpace: 'nowrap' }}>
          <Button variant="warning" onClick={() => handleReportClickmainreportasmzsm(user._id)}>
            Wallet Report
          </Button>
        </td>
      </tr>

      {isExpanded && (
        <tr>
          <td colSpan="11" className="p-0">
            <div
              className="ps-4 border-start border-primary"
              style={{ marginLeft: `${level * 20}px` }}
            >
              {loading ? (
                <div className="text-center py-3" style={{ color: "var(--bs-primary2)" }}>
                  Loading...
                </div>
              ) : subUsers.length > 0 ? (
                <SubUserTable
                  users={subUsers}
                  totalCount={subUserTotalCount}
                  onUserClick={onUserClick}
                  onTransferClick={onTransferClick}
                  onReverseTransferClick={onReverseTransferClick}
                  onReportClick={onReportClick}
                  onPageChange={handleSubUserPageChange}
                  level={level + 1}
                  initialPage={currentPage}
                  initialPageSize={pageSize}
                  handleReportClickmainreportasmzsm={handleReportClickmainreportasmzsm}
                />
              ) : (
                <div className="text-center py-3" style={{ color: "var(--bs-primary2)" }}>
                  No downstream members available
                </div>
              )}
            </div>
          </td>
        </tr>
      )}
    </React.Fragment>
  );
};

export { SubUserTable, SubUserRow };
